body{
  background-color: rgb(97, 116, 115);
  color: white;
  margin: 20px;
}
.game {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.board-row{
  display: flex;
  flex-flow: row nowrap;
}
.square{
  background-color: rgb(30, 79, 164);
  color: white;
  border: 1px solid #999;
  padding: 0;
  font-size: 84px;
  text-align: center;
  width: 100px;
  height: 100px ;
}
.square:focus{
  outline: none;
  background-color: #222;
}
.status{
  margin: 20px;
  font-size: 24px;
}
.game{
  margin: 20px;
  font-size: 30px;
}